html {
  color: #060b35;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

h2 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  object-fit: contain;
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:nth-child(n+2) {
  margin-left: 20px;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  text-align: center;
  padding: 23px 0;
  line-height: 14px;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.main-content {
  grid-template-columns: 200px;
  justify-content: center;
  gap: 48px 46px;
  padding: 50px 40px 72px;
  display: grid;
}

@media (width >= 488px) {
  .main-content {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main-content {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main-content {
    grid-template-columns: repeat(4, 200px);
  }
}

.image {
  width: 160px;
  height: 134px;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image {
  margin: auto;
  padding-bottom: 40px;
}

.card__title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: all .3s;
}

.card__product-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  margin-bottom: 24px;
}

.card__price {
  margin-bottom: 16px;
}

.card:hover .card__title {
  color: #34568b;
}

.rating {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.rating__reviews {
  color: #060b35;
  text-align: right;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.stars {
  display: flex;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.price {
  justify-content: space-between;
  display: flex;
}

.price__text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.price__amount {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.button {
  cursor: pointer;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: all .3s;
}

.button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

